import { memo, forwardRef, useMemo, ComponentProps } from "react";
import styled from "styled-components";
import { motion } from "motion/react";
import { CollapseProvider, useCollapse } from "./useCollapse";

type MotionDiv = ComponentProps<typeof motion.div>;

export interface CollapseProps
  extends Omit<MotionDiv, "title" | "style" | "onClick"> {
  /**
   * @default: undefined
   * @description if provided, controlled by open
   */
  open?: boolean;
  /**
   * @default: true
   * @description if open provided, controlled by open
   */
  defaultOpen?: boolean;
  /** @description title component  */
  title: React.ReactNode;
  /** @description collapse content */
  children: React.ReactNode;
  /** @description extra content will be put into wrapper */
  extraNode?: React.ReactNode;
  /** @description styles for header wrapper */
  headerStyles?: React.CSSProperties;
  /**
   * @description styles for content wrapper
   * @note do not set margin and padding, browser won't count these values and will cause Collapse height error.
   */
  contentStyles?: Omit<
    React.CSSProperties,
    | "padding"
    | "paddingTop"
    | "paddingBottom"
    | "margin"
    | "marginTop"
    | "marginBottom"
  >;
  /** @default false */
  disableHeaderToggle?: boolean;
}

export const Collapse = forwardRef<HTMLDivElement, CollapseProps>(
  ({ defaultOpen = true, ...props }, ref) => {
    return (
      <CollapseProvider
        initialState={{ open: props.open, defaultOpen: defaultOpen }}
      >
        <CollapseContent {...props} defaultOpen={defaultOpen} ref={ref} />
      </CollapseProvider>
    );
  }
);
export const CollapseContent = forwardRef<HTMLDivElement, CollapseProps>(
  (props, ref) => {
    const {
      title,
      headerStyles,
      contentStyles,
      children,
      extraNode,
      disableHeaderToggle,
      open: propsOpen,
      defaultOpen,
      ...rest
    } = props;
    const {
      isOpen,
      // collapseHeightAnimation,
      onAnimationEnd,
      toggle,
      headerRef,
      contentRef,
      initialed,
      headerHeight,
      contentHeight,
    } = useCollapse();
    const open = typeof propsOpen === "boolean" ? propsOpen : isOpen;
    // styles
    const contentStyle = useMemo(() => {
      if (!headerHeight || !contentHeight)
        return {
          opacity: 0,
          pointerEvents: "none",
        } satisfies React.CSSProperties;
      const s: React.CSSProperties = contentStyles || {};
      return { ...s, top: headerHeight };
    }, [contentStyles, headerHeight, contentHeight]);
    const height = open ? headerHeight + contentHeight : headerHeight;
    return (
      <Wrapper
        {...rest}
        ref={ref}
        animate={{ height }}
        transition={initialed ? undefined : { duration: 0 }}
        onAnimationComplete={(event) => {
          rest?.onAnimationComplete?.(event);
          onAnimationEnd();
        }}
      >
        {/* absolute content */}
        <ContentWrapper
          ref={(element) => element && contentRef(element)}
          style={contentStyle}
        >
          {children}
        </ContentWrapper>
        {extraNode}
        {/* header */}
        <HeaderWrapper
          ref={(element) => element && headerRef(element)}
          style={headerStyles}
          onClick={disableHeaderToggle ? undefined : toggle}
        >
          {title}
        </HeaderWrapper>
      </Wrapper>
    );
  }
);

export default memo(Collapse);

export const Wrapper = styled(motion.div)`
  position: relative;
  overflow: hidden;
`;

export const HeaderWrapper = styled.div`
  ${(p) => p.theme.flexCSS.row}
  align-items: center;
  position: relative;
`;

export const ContentWrapper = styled(motion.div)`
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
`;
